@media (min-width: 768px) {

  .md\:left-0 {
    left: 0px;
  }

  .md\:top-0 {
    top: 0px;
  }

  .md\:hidden {
    display: none;
  }

  .md\:w-\[25\%\] {
    width: 25%;
  }

  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .md\:border {
    border-width: 1px;
  }

  .md\:p-12 {
    padding: 3rem;
  }

  .md\:text-3xl {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .md\:text-6xl {
    font-size: 3.75rem;
    line-height: 1;
  }
}
@media (min-width: 1024px) {

  .lg\:block {
    display: block;
  }

  .lg\:w-\[25\%\] {
    width: 25%;
  }

  .lg\:w-\[75\%\] {
    width: 75%;
  }

  .lg\:text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}
